.location_container {
    background: #fff;
    border: 1px solid rgba(34, 36, 38, 0.15);
    border-radius: 5px;
    height: 100%;
    width: 100%;
    padding: 10px;
    display: grid;
    grid-gap: 0.5em;
    grid-template-rows: max-content auto;
}

.location_content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    text-align: center;
}

.location_number_container {
    border: 1px solid rgba(34, 36, 38, 0.15);
    border-radius: 10px;
    padding: 10px;
}

.location_number_sub_container {
    display: grid;
    border: 1px solid rgba(34, 36, 38, 0.15);
    border-radius: 10px;
    display: grid;
    grid-gap: 0.5em;
    padding: 20px;
}

.location_number_input_group {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 20px;
}

.location_button {
    border-radius: 25px !important;
}

.location_content_wrapper {
    display: grid;
    grid-gap: 0.5em;
}

.location_content_container {
    display: grid;
    grid-gap: 0.5em;
    grid-template-columns: repeat(3, 1fr);
}