.home_container {
    display: grid;
    grid-gap: 0.5em;
    grid-template-columns: auto 35%;
    width: 100%;
    height: 100%;
}

.home_content {
    border: 1px solid rgba(34, 36, 38, 0.15);
    border-radius: 5px;
    background: #fff;
    padding: 10px;
}

.home_content_header {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
}

.home_icon_container {
    display: grid;
    grid-template-columns: max-content max-content;
    grid-gap: 00.5em;
    align-items: center;
}